.product-card {
  padding: 1em;
  border: 0 !important;
  height: auto;
  box-sizing: border-box;
  border-radius: 0.4em;
  margin-bottom: 20px;
  position: relative;
}
.product-card-details img {
  width: auto;
  max-width: 200px;
}

.prodcut-container {
  height: fit-content !important;
  border-bottom: 1px solid rgb(128, 128, 128, 0.5);
  
}

.w-20 {
  width: 20%;
}

.tag {
  color: gray;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
}
.grid-filter-options {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;
}
.grid-filter-options .filter {
  margin: 0;
  cursor: pointer;
  background-color: white;
  padding: 10px;
  border-radius: 5px;
}

@media only screen and (max-width: 776px) {
  .prodcut-container {
    padding-top: 0;
  }
  .prodcut-container .w-20 {
    display: none;
  }

  .prodcut-container .w-75 {
    width: 100%;
  }

  .prodcut-container .products {
    display: grid;
    grid-template-columns: 1fr !important;
    justify-items: center;
  }

  .products .product-card {
    background-color: #eeedeb;
    width: calc(100% - 50px);
  }

  .prodcut-container > div {
    flex-direction: column;
  }

  .prodcut-container .mobile {
    padding-inline: 25px;
    box-sizing: border-box;
    height: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  
  .sideMenu {
    width: 90% !important;
    height: 100%;
  }

  .filter-sideMenu .filters-container {
    width: 90%;
  }

  .filter-sideMenu .select-option {
    color: gray !important;
    width: 100%;
  }


  .prodcutDetails {
    display: flex;
    flex-direction: column;
    height: fit-content;
    padding: 25px;
    box-sizing: border-box;
  }
  .details-left,.details-right {
    height: fit-content;
    width: 100%;
    position: relative;
    display: flex;
    
    padding: 0;
  }


  .prodcutDetails {
    padding-bottom: 50px;
    overflow: hidden;
  }
  .details-right {
    padding: 0;
  }

  .details-left {
    height: 350px;
    background-color: #eeedeb;
    margin-top: 80px;
    margin-bottom: 20px;
  }
  .details-left img:first-child{
    position: relative;
    transform: unset;
    left: 0;
    top: 0% !important;
    
  }

  .reviews-container  {
    overflow: scroll;
  }


}


.sideMenu {
  padding: 20px;
  box-sizing: border-box;
}
.sideMenu > div {
  overflow-x: hidden;
  justify-content: space-between;
}