.section {
  height: 100vh;
  width: 100vw;
}

.home-right {
  position: absolute;
  right: 0;
  width: 50vw;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: end;
}

.back-circle {
  position: relative;
  height: 90vh;
  width: 25vw;
  background-color: #00add8;
  right: 100px;
  bottom: -50px;
  border-radius: 30px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  z-index: 1;
}

.back-circle img {
}

.card-bubble {
  position: absolute;
  width: 150px;
  height: 150px;
  background-color: white;
  top: 20px;
  left: -50px;
  border-radius: 5px;
  z-index: 0;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.card-bubble2 {
  right: -20px;
  top: unset;
  left: unset;
  bottom: 150px !important;
  z-index: 3;
}

.back-img {
  position: absolute;
  right: 20px;
  top: 0px;
  width: 100%;
  transform: scaleX(-1);
  z-index: 2;
}

.section2-left {
  width: 35%;
}
.section2-right {
  width: 50%;
}

.section2-left img {
  width: 70%;
  height: 80%;
  z-index: 1;
}

.section2-left img.back {
  z-index: 0;
  width: 100%;
  opacity: 0.1;
  height: 300px;
  top: -90px;
}

.section3 {
}
.section3-content {
  height: calc(100% - 80px);
}

.section3-banner {
  height: 80px;
  position: absolute;
  left: 0;
  width: 100vw !important;
  background-color: #343131;
}

.section3-right {
  width: 30%;
}

.section3-right img {
  position: relative;
}

.section3-left .w-1-2 {
  width: 45%;
}

.faq-item {
  width: 50%;
}

.faq-question + img,
.navs img {
  width: 20px !important;
  opacity: 0.5;
}
.navs img {
  position: relative;
  top: 1px;
}

.hover-component {
  top: calc(100% + 20px);
}
.navs .hover-component .grid {
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: auto;
  grid-gap: 10px;
  height: fit-content;
}
.category-item {
  height: 60px;
  padding-inline: 10px;
  box-sizing: border-box;
  border-radius: 5px;
}
.category-item img {
  opacity: 1;
  width: 40px !important;
}

.category-item p {
  text-decoration: underline;
}
.section5-left {
  width: 60%;
  height: 90%;
}

.section5-right {
  width: 30%;
  height: 90%;
  position: relative;
}
.section5-right img {
  width: 80%;
  position: relative;
}

.section5-content {
  height: 100%;
}

.section5-product {
  width: 100%;
  border-radius: 5px;
  height: calc(100% / 3.4);
}

.product-img {
  width: 80px;
}
.product-img img {
  width: 100%;
}

.product-texts {
  width: 60%;
}

/* footer */
.footer {
  display: flex;
  flex-direction: column;
}

.footer-top {
}

.footer-top-left {
}

.footer-top-left > h1 {
  font-size: 38px;
}

.footer-top-right {
}

.send-message-btn {
  border-radius: 43%;
  margin-left: -55px;
  height: 35px;
  position: relative;
  top: 6px;
  left: 1px;
  z-index: 1;
}

.footer-bottom {
  padding: 50px 20px;
}

.footer-bottom h3 {
  font-size: 23px;
}

.footer-bottom ul {
  list-style: none;
  margin: 10px 0;
  font-size: 16px;
}

.footer-bottom ul li {
  margin-bottom: 10px;
  cursor: pointer;
}
.footer-bottom ul li:hover {
  color: #00add8 !important;
}
.footer-bottom :last-child ul li:hover {
  color: #00add8 !important;
}

.social-link :hover {
  background-color: #c8c8c6;
}

/* end footer */

.products {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 40px;
}

.product-details {
  width: 100vw;
  height: 100vh;
  margin: 0;
  top: 0 !important;
}

.details-left img {
  left: 100%;
  transform: translateX(-50%);
}

.details-right {
  padding-right: 0 !important;
  padding-left: 150px;
}

.reviews-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}
.review-container {
  height: 120px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.details-left .back-text {
  font-size: 4rem;
  width: fit-content;
  height: fit-content;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0.1;
}

.back-text p {
  transform: rotate(270deg);
  text-align: center;
}

.details-left img:nth-of-type(2) {
  left: 10% !important;
  top: 10px;
  opacity: 0.1;
  transform: none;
  width: 80%;
}

.social-icon {
  width: 25px;
}

.preloader-container {
  z-index: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 50px;
  left: 0;
  width: 100%;
  height: 100vh;
}

.preloader-container img {
  position: relative;
}


.home-logo {
  width: 100% !important;
  position: relative;
  margin-bottom: 5px;
}

.home-logo img {
  width: 100px;
  position: relative;
  left: -15px;
}
/*responsive*/

@media only screen and (max-width: 776px) {
  .web {
    display: none;
  }
  .header {
    padding-inline: 30px;
  }

  .header .action-button {
    display: none;
  }

  .mobile-nav-toggle {
  }

  .mobile {
    display: flex;
  }
  .section {
    padding: 0;
  }

  .home {
    display: flex;
    justify-content: center;
    padding-top: 80px;

    padding-bottom: 20px;
  }
  .home .bg-grey {
    width: 95vw;
    position: relative;
    padding: 20px;
    border-radius: 10px;
    background-color: #00add8;
  }

  .home-left {
    width: 100vw !important;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: 50% 10%;
    opacity: 1;
    color: white;
    justify-content: center;
  }
  .home-left p {
    width: 100%;
  }

  .home-left .small-hr {
    background-color: white;
  }
  .home-right {
    display: none;
  }
  .home .action-button {
    background-color: white;
  }
  .home .action-button .btn {
    color: #00add8 !important;
  }

  .home .mobile {
    height: 200px;
    position: relative;
  }

  .home-left .mobile img {
    width: 300px;
  }

  .section2 {
  }
  .section2-left {
    display: none;
  }
  .section2-right {
    width: 100%;
    padding: 25px;
  }

  .section3-right {
    display: none;
  }

  .section3-left {
    width: 100%;
    padding: 25px;
  }

  .section4-content {
    padding: 25px;
  }

  .section4 .sec-text {
    width: 100%;
  }
  .section4-content p {
    width: 90%;
  }

  .faq-item {
    width: 90%;
    min-height: fit-content;
  }

  .section5 {
    padding-inline: 25px;
    padding-bottom: 50px;
    min-height: fit-content;
    height: 100%;
    position: relative;
  }
  .sec-text {
    width: 100% !important;
    font-size: 2.5rem;
  }

  .section5-right {
    display: none;
  }
  .section5-left {
    width: 100%;
  }

  .section5-product {
    height: 300px;
    min-height: fit-content;
    margin-top: 20px;
    flex-direction: column;
    align-items: center;
    background-color: transparent;

    box-shadow: rgba(0, 0, 0, 0.03) 0px 5px 15px;
  }

  .section5-product .product-texts {
    width: 100%;
    text-align: center;
    margin-bottom: 20px;
  }
  .product-img {
    width: 150px;
    margin-bottom: 15px;
    background-color: transparent;
  }

  .footer {
    height: 100%;
    min-height: fit-content;
    padding-bottom: 30px;
  }

  .footer-top {
    flex-direction: column;
  }

  .footer-top-left,
  .footer-top-right {
    width: 100% !important;
  }

  .footer-top-left p {
    width: 100% !important;
  }

  .footer-bottom {
    display: grid;
    grid-template-columns: 1fr;
  }
  .footer-bottom > div {
    width: 100%;
    text-align: center;
    padding-bottom: 20px;
    padding-top: 20px;
    border-bottom: #fff 2px solid;
  }

  .footer .contact-last ul {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .footer .contact-last li {
    width: 200px;
    justify-content: space-between;
    position: relative;
  }

  .footer li div {
    width: 70%;
    text-align: start;
  }

  .footer-end {
    flex-direction: column;
  }

  .footer-end-left {
    display: grid;
    grid-template-columns: 1fr 1fr;
    width: 100%;
    justify-items: center;
  }

  .footer-end-left > div {
    width: 100px;
    margin-bottom: 20px;
  }

  .footer-end-right {
    width: 100%;
    padding-inline: 25px;
    box-sizing: border-box;
  }

  .mobile .nav-item {
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

/*end responsive*/

.mobile-nav-menu {
  top: 100%;
  border-radius: 0% !important;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 0px;
  background-color: white;
}


.mobile-nav-menu2 .grid {
  z-index: 5;
  box-sizing: border-box;
  padding: 25px;
  display: grid;
  grid-template-columns: 1fr 1fr;
}